.list {
    list-style: none;
    padding: 0;
    margin-left: 80px;

    &__item {
        display: inline-block;
        margin-right: 50px;
    }

    &__link {
        color: #818181;
        text-decoration: none;
        font-size: 1.4rem;
    }

    &__linkActive {
        font-weight: 700;
    }
}